.navbar-start-anim {
  animation: navbar-anim 1.6s ease-in-out;
}

.navbar-menu-in {
  animation: navbar-menu-in 1.6s ease-in-out;
}

@keyframes navbar-anim {
  0% {
    transform: translateY(-50%);
    opacity: 0;
  }
  100% {
    transform: unset;
    opacity: 1;
  }
}

@keyframes navbar-menu-in {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}
